import React from 'react'
import ReactDom from 'react-dom'
import cx from 'classnames'
import debounce from '../../lib/debounce'
import './style.scss'

const defaultColor = ['#00aaff']

export default class RingProgress extends React.Component {
  static defaultProps = {
    value: 0.6653321,
    strokeWidth: 20,
    color: defaultColor,
    strokeLinecap: 'butt',
    showPercent: true,
    fixed: 1,
    style: {}
  }

  constructor (props) {
    super(props)
    this.state = {
      mounted: false
    }
  }

  componentDidMount () {
    const root = ReactDom.findDOMNode(this)
    this.observer = new window.ResizeObserver(debounce(entries => {
      entries.forEach((entry) => {
        this.setState({
          width: entry.target.offsetWidth,
          height: entry.target.offsetHeight
        })
      })
    }))
    this.observer.observe(root)

    this.setState({ width: root.offsetWidth, height: root.offsetHeight, mounted: true })
  }

  getColor = () => {
    const color = this.props.color
    if (!color || (Array.isArray(color) && color.length === 0)) return defaultColor
    if (typeof color === 'string') return [color]
    return color
  }

  componentWillUnmount () {
    this.observer.disconnect()
    this.observer = null
  }

  render () {
    const { strokeWidth, className, style, color, showPercent, value, strokeLinecap, fixed, labelClassName } = this.props
    const { width, height, mounted } = this.state
    const halfWidth = width * 0.5
    const halfHeight = height * 0.5

    const r = Math.min(halfWidth, halfHeight) - strokeWidth * 0.5 - 1
    const perimeter = Math.PI * 2 * r
    const strokeDasharray = Math.round(perimeter * value) + ' ' + Math.ceil(perimeter)

    return (
      <div className={cx('dui-ring-progress', className)} style={style}>
        {
          mounted &&
          <svg width={width} height={height} viewport={`0 0 ${width} ${height}`}>
            <defs>
              <linearGradient id='dui-ring-progress-gradient' x1='0' x2='0' y1='1' y2='0'>
                {
                  this.getColor().map((v, i) => {
                    let interval = Math.round(100 / (color.length - 1))
                    let offset = interval * i + '%'
                    if (i === color.length - 1) {
                      offset = '100%'
                    }
                    return <stop key={i} offset={offset} stopColor={v} />
                  })
                }
              </linearGradient>
            </defs>
            <circle
              className='dui-ring-progress-trail'
              fill='none'
              strokeWidth={strokeWidth}
              cx={halfWidth}
              cy={halfHeight}
              r={r}
            />
            {
              <circle
                transform={`rotate(-90, ${halfWidth}, ${halfHeight})`}
                className='dui-ring-progress-path'
                stroke='url(#dui-ring-progress-gradient)'
                fillOpacity={0}
                strokeWidth={strokeWidth}
                cx={halfWidth}
                cy={halfHeight}
                r={r}
                strokeLinecap={value >= 1 / Math.pow(10, fixed + 2) ? strokeLinecap : undefined}
                strokeDasharray={strokeDasharray}
              />
            }
          </svg>
        }
        {
          showPercent &&
          <label className={cx('dui-ring-progress-label', labelClassName)} style={this.props.labelStyle}>
            {(value * 100).toFixed(fixed) + '%'}
          </label>
        }
      </div>
    )
  }
}
